body{
  color: #ffffff;
  background: #222;
  font-family: 'Monospace';
  margin-left:  5px;
  margin-right: 5px;
  margin-top: 5%;  
}

a{
  color: #ffffff;
  font-family: 'Monospace';
}

button{
  border-style: solid;
  background-color: #ffffff; 
  color: #222;
  font-family: 'Monospace';
  font-size: 16px;
  padding: 1%;
}

input[type=file]::file-selector-button ,input[type=file]{
  border-style: none;
  background-color: #ffffff; 
  color: #222;
  font-family: 'Monospace';
  font-size: 16px;
  padding: 1%;
}

button:hover,input[type=file]::file-selector-button:hover {
  text-decoration: underline;
}

button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

ul{
  width:fit-content;
  display: block;
  margin-left: auto;
  margin-right: auto;
 }
 
channel-title{
  padding-left: 5px;
  padding-right: 5px;
  margin: auto;
  display: block;
}

channel-box{
  border-style: solid;
  margin-left:  auto;
  margin-right: auto;
  width:fit-content;
  height: 500px;
  display: grid;
  grid-gap: 5px;
  overflow-y:scroll;
}

upload-block{
  padding-top: 20%;
}

channel-card{
  width: fit-content;
  height: fit-content;
  display: grid;
  grid-template-columns:auto auto auto;
  margin-right: auto;
}

error-text{
  display: block;
  margin-left: auto;
  margin-right: auto;
  color: #ff0033;
  text-align: center;
  margin-top: 10px; 
}

.checkboxColumn{
  display: block;
  margin: auto;
  margin-left: 5px;
  margin-right: 5px;
}

.center{
  text-align: center;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.channelThumbnail{
  background:#ffffff;
  color: #000000;
  font-size: 10px;
  overflow-wrap:break-word;
  overflow: hidden;
}